.calculator{
  display: flex;
  max-width: 1180px;
  margin: 120px auto;
  box-shadow: 0px 2px 16px rgba(32, 108, 164, 0.16);
  padding: 60px 30px;
  border-radius: 8px
}
.calculator__wrapper{
  max-width: 980px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}
.calculator__container{
  display: flex;
  flex-wrap: wrap;
}
.calculator__container--padding{
  padding: 20px 0;
}
.calculator__container select + select {
  margin: 0 0 0 25px;
}
.calculator__container--column{
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 0;
}
.calculator__container--max-width{
  max-height: 165px;
}
.calculator__title{
  font-size: 32px;
  line-height: 38px;
  color: #111563;
}
.calculator__subtitle{
  font-size: 18px;
  line-height: 150%;
  color: #63718E;
}
.caltulator__select{
  padding: 5px 40px 5px 20px;
  background-color: #F9FAFC;
  border-radius: 10px;
  border: none;
  box-shadow: 0px 1px 3px rgba(5, 41, 83, 0.14);
}
.caltulator__select--second{
  padding: 10px 168px 15px 20px;
}
.calculator__couter{
  background: #F9FAFC;
  box-shadow: 0px 1px 3px rgba(5, 41, 83, 0.14);
  border-radius: 10px;
  padding: 10px 20px;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.couter__minus{
  margin: 0 10px 0 0;
}
.couter__plus{
  margin: 0 0 0 10px;
}
.calculator__couter button{
  border: none;
  font-size: 18px;
  background-color: inherit;
}
.calculator__date{
  background: #F9FAFC;
  box-shadow: 0px 1px 3px rgba(5, 41, 83, 0.14);
  border-radius: 10px;
}
.calculator__sum{
  font-size: 22px;
  line-height: 23px;
  display: flex;
  font-weight: bold;
  align-items: center;
  text-align: center;
  color: #C774EE;
  margin: 0 0 0 20px;
  align-self: center;
  padding: 23px 0;
}
.calculator__btn{
  color: white;
  background: #C774EE;
  box-shadow: 0px 2px 16px rgba(32, 108, 164, 0.16);
  border-radius: 10px;
  padding: 14px 50px;
  border: 0;
  cursor: pointer;
  align-self: center;
  margin: 0 0 0 20px;
}
@media (max-width: 767px){
  .caltulator__select{
    width: 46%;
    padding: 12px 100px 12px 20px;
  }
  .calculator__container{
    width: 100%;
    justify-content: space-between;
  }
  .calculator__couter{
    width: 46%;
    margin: 0;
  }
  .calculator__date{
    width: 46%;
  }
  .calculator__container--column{
    flex-direction: row;
    justify-content: center;
  }
}
@media (max-width: 480px){
  .calculator__container{
    flex-direction: column;
  }
  .calculator__couter{
    width: 100%;
    margin: 0;
  }
  .calculator__date{
    width: 100%;
  }
  .calculator__couter{
    margin: 0 0 20px 0;
  }
  .calculator__container select + select{
    margin: 20px 0 0 0;
  }
  .caltulator__select{
    width: 100%;
    margin: 0;
    padding: 12px 100px 12px 20px;
  }
  .calculator__btn{
    width: 100%;
    margin: 0;
  }
  .calculator__sum{
    margin: 0;
  }
}