.main__container--img1{
  background:
    url(../Assets/iMac.png) center/cover no-repeat,
    url(../Assets/set.svg) center/cover no-repeat;
  min-height: 500px;
  margin: 20px 30px;
}
.main__container--img2{
  background: 
    url(../Assets/Watch.png) center/cover no-repeat, 
    url(../Assets/requirements-image.svg) center/contain no-repeat;
  min-height: 500px;
  margin: 20px 30px;
}
.main__container{
  width: 50%;
}

.step__subtitle{
  font-size: 18px;
  line-height: 150%;
  text-align: center;
  color: #63718E;
  margin: 15px 0;
}
.main__row{
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1220px;
  margin: 0 auto;
  padding: 40px 20px;
}
.main__img{
  width: 630px;
  height: 400px;
}
.main__title{
  font-size: 42px;
  line-height: 120%;
  color: #111563;
}
.main__subtitle{
  font-size: 18px;
  line-height: 150%;
  color: #63718E;
  margin: 20px 0;
}
.main__btn{
  padding: 16px 80px;
  background-color: #C774EE;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-size: 20px;
}
@media (max-width: 767px){
  .main__row{
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .main__container{
    width: 100%;
  }
  .main__row--reverse{
    display: flex;
    justify-content: center;
    flex-direction: column-reverse;
  }
}