.faq{
  max-width: 1044px;
  margin: 0 auto;
  padding: 0 20px 160px 20px;
}
.faq__title{
  font-weight: bold;
  font-size: 32px;
  line-height: 38px;
  color: #111563;
}
.faq__btn{
  align-self: center;
  padding: 15px 40px;
  background: #C774EE;
  border-radius: 8px;
  border: none;
  color: white;
  position: relative;
left: 50%;
transform: translate(-50%, 0);
}