.info{
  box-shadow: 0px 2px 16px rgba(32, 108, 164, 0.16);
  border-radius: 8px;
  padding: 10px 20px;
  margin: 20px 0;
  cursor: pointer;
}
.info__container{
  display: flex;
  justify-content: space-between;
}
.info__title{
  font-weight: bold;
  font-size: 22px;
  line-height: 30px;
  color: #111563;
}
.info__text{
  font-size: 18px;
  line-height: 150%;
  color: #63718E;
}
.close{
  transform: rotate(0deg);
  transition: 0.5s ease;
}
.open{
  transform: rotate(180deg);
  transition: 0.5s ease;
}
